@import url(https://fonts.googleapis.com/css2?family=Comfortaa&family=RocknRoll+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comfortaa&family=RocknRoll+One&display=swap);
body {
  font-family: "Comfortaa", sans-serif;
  background-color: beige;
  text-align: center;
}

button {
  font-family: inherit;
  padding: 1rem;
  margin: 1rem;
  background: white;
  box-shadow: 0 0 5px #333;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  text-transform: uppercase;
}

.circle {
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;

  height: 5em;
  width: 5em;

  border-radius: 50%;
  display: inline-block;
  margin: 1rem;
}

.green {
  background-color: green;
}

.violet {
  background-color: violet;
}

.red {
  background-color: red;
}

.orange {
  background-color: orange;
}

.blue {
  background-color: blue;
}

.pink {
  background-color: rgb(243, 72, 129);
}

.active {
  background-image: url(data:image/png;base64,);
}

#circles {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  margin-bottom: 40px;
}

.overlay {
  position: fixed;
  display: grid;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  justify-items: center;
  -webkit-align-items: center;
          align-items: center;
}

.gameover_box {
  background: white;
  padding: 1rem;
  width: 40%;
  min-height: 25%;
  border-radius: 5px;
  position: relative;
  display: grid;
  justify-items: center;
  -webkit-align-items: center;
          align-items: center;
}

.gameover_box button {
  background: lightgrey;
  position: absolute;
  top: 5px;
  right: 5px;
  cursor: pointer;
}

body {
  background: linear-gradient(
    90deg,
    #2905f8,
    #1ff354,
    #9ff80f,
    rgb(252, 166, 6)
  );
  background-size: 180%;
  -webkit-animation: gradient 10s ease infinite;
          animation: gradient 10s ease infinite;
  background-repeat: no-repeat;
  background-attachment: fixed;
  color: rgb(31, 30, 30);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 20px;
  font-family: "RocknRoll One", sans-serif;
}
@-webkit-keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

button {
  padding: 1rem;
  background-color: rgb(19, 17, 129);
  color: white;
  font-size: 20px;
  font-family: "RocknRoll One", sans-serif;
  border-radius: 0.5rem;
  -webkit-border-radius: 0.5rem;
  -moz-border-radius: 0.5rem;
  -ms-border-radius: 0.5rem;
  -o-border-radius: 0.5rem;
  margin: 1rem;
}

h1,
p {
  font-family: "Comfortaa", cursive;
}

.center {
  text-align: center;
}

